//------------------------------------------------------
// Ion Components Overridden
//------------------------------------------------------
ion-toolbar {
  --ion-background-color: #eee;
  --background: #eee;
  border: none;
  --border-width: 0px !important;
  height: 48px; // Sets height of header menu buttons
}

ion-title.title-default {
  padding: 0px;
  font-weight: normal;
  text-transform: uppercase;
  color: #424242;
}

ion-content {
  --ion-background-color: #fff;
  --background: #fff;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  outline: none;

  .message-list {
    width: 100%;
  }
}

ion-modal.mini-modal {
  --mode: ios;
  --show-backdrop: true;
  --backdrop-opacity: 0.4 !important;
}

.picker-columns.sc-ion-picker-ios {
  perspective: unset;
}

ion-input {
  outline: none;
  border-radius: 4px;
  min-height: 36px;
}

ion-footer {
  --ion-background-color: #eee;
  --background: #eee;
  display: flex;
  padding: 0.3em;

  .button-solid {
    min-height: 46px;
    width: 100%;
    font-weight: 1;
  }

  .button-disabled {
    cursor: default;
    opacity: 0.2;
    pointer-events: none;
  }

  .apply-button {
    --background: var(--ion-color-secondary);
  }

  .delete-button {
    --background: #d9534f;
  }

  .cancel-button {
    font-weight: 1;
  }
}

ion-tab-bar {
  border: unset;
  margin: 6px;
  height: 50px; // Sets height of footer page buttons
}

ion-tab-button {
  font-size: 12px;

  ion-label {
    text-transform: uppercase;
    // color: #424242;
  }
}

ion-row {
  // margin: 8px auto;
  align-items: center;

  ion-col {
    font-weight: normal;
    font-size: 14px;
  }
}

ion-button {
  outline: none;
  --border-radius: 4px;
}

button {
  border-radius: 4px !important; // overwrites internal button styles
  outline: none;
}

.icon-right-of-checkbox {
  padding: 0px 0px 4px 4px;
}

.debug-info {
  float: right;
  color: gray;
  font-size: 12px;
  margin-right: 10px;
}

//------------------------------------------------------
// Schedule Page Styles
//------------------------------------------------------
#schedule-page {
  .center {
    margin-left: auto;
    margin-right: auto;
    display: block;
  }

  .filter-not-applied {
    display: none;
  }

  .filter-applied {
    font-size: 84px;
    color: red;
    position: absolute;
    top: -30px;
    right: 3px;
  }
}

// Add Daily Note Button next to week header event list
.add-daily-note-button {
  color: #fff;
  background-color: #424242;
  font-size: 12px;
  padding: 3px 6px;
  float: right;
  outline: none;
}

.button-selectEmployees-messages {
  height: 30px;
}

//---------------------------------------------------
// Week Picker Control - Schedule Page
//----------------------------------------------------
.chooser {
  background: var(--ion-item-background, var(--ion-background-color, #fff));

  .top {
    margin: 6px 0px;
    display: flex;
    align-items: center;
    width: 100%;

    .arrow {
      text-align: center;
      font-size: 25px;
      background-color: #eee;
      outline: none;
    }

    .month-year-container {
      text-align: center;
      flex: 1;
      text-transform: uppercase;

      .datetime-picker-container {
        width: 100%;
        display: block;

        .datetime-picker {
          display: inline-block;
          vertical-align: middle;
        }
      }
    }
  }

  .weekday-list {
    display: flex;
    width: 100%;
    font-size: 14px;
    margin: 4px auto;

    .weekday-button {
      flex: 1;
      text-align: center;
      padding: 0 10px 0 5px;
      background-color: #eee;
      line-height: 1.4;

      .today {
        border-bottom: 2px solid #ddd;
      }

      .not-today {
        border-bottom: 2px solid #eee;
      }
    }
  }
}

//---------------------------------------------------
// CARDS
//---------------------------------------------------
.card {
  margin: 2px auto;
  padding: 8px;
  font-family: Helvetica, Arial, sans-serif;
  display: flex;
  align-items: center;
  background-color: #fff;
  line-height: 1.4;
  width: 100%;

  .card-first-col {
    min-width: 30px;
    font-size: 14px;
    display: flex;
    align-items: stretch;
    flex-direction: column;
  }

  .card-second-col {
    font-size: 14px;
    line-height: 1.4;
    margin-left: 10px;
    text-align: left;
    padding: 0px 10px;
    white-space: pre-line;

    .dept-position-row {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }

    .note-text-row-oneline {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }

    .break-div {
      color: #bbb;
      margin: 3px 0px;
      width: 100%;
    }
  }
}

//--------------------------------------------------
// Daily Notes
//---------------------------------------------------
.daily-note {
  .card-second-col {
    border-left: 7px solid #424242;
  }
}

.IonTitle--DailyNote {
  background: #eee;
}

// Edit Daily Note Modal
.edit-daily-modal-content {
  ion-textarea {
    margin: 0px;
    display: block;
  }

  .character-counter {
    float: right;
    margin: 10px 20px;
    color: #aaa;
  }
}

//--------------------------------------------------
// Events
//--------------------------------------------------
.event-list-header {
  color: #424242;
  padding: 20px 10px 10px;
  font-size: 16px;
  border-bottom: 1px solid #ddd;
  display: block;
  outline: none;
}

.event-list-header:focus {
  animation: highlight 1500ms ease-out;
}
@keyframes highlight {
  0% {
    background-color: #ddd;
  }
  100 {
    background-color: white;
  }
}

//---------------------------------------------------
// Edit Event Modal
//---------------------------------------------------
.outer-content {
  font-size: 14px;

  ion-row {
    ion-col {
      .label {
        color: #aaa;
      }
      P .select {
        border: 1px solid #ccc;
        padding: 5px;
        margin: 0;
      }

      // Event Notes
      .textarea {
        border: 1px solid #ccc;
        margin: 0px;
        height: 86px;
        padding: 8px;

        .textarea-wrapper {
          textarea {
            padding: 0px;
          }
        }
      }

      .input-textbox {
        border: 1px solid #ccc;
        height: 32px;
      }
    }
  }

  .shadow-event-label {
    margin-left: 10px;
  }

  .grid {
    .current-event-row {
      .current-event-col {
        display: inherit;
      }
    }
  }
}

.invisible {
  display: none;
}

.request-button {
  margin: 20px 15px 0 10px;
  font-size: 16px;
}

// Weekday List
//---------------------------------------------------
.weeklist-container {
  display: flex;

  .day {
    margin: auto;
    text-align: center;

    .day-checkbox {
      margin-right: 1px;
    }
  }
}

// Recurring Event Section
//---------------------------------------------------
.alert-wrapper {
  width: 450px !important;
}

// Event Request Modal
//---------------------------------------------------
.checkbox {
  padding: 0;
  margin: 5px 0 5px 10px;
  transform: scale(1.5);
}

.textarea {
  border: 1px solid #ccc;
}

.usage-message {
  margin: 20px 10px 10px 10px;
  font-size: 24px;
}

// Event Break Editor
//---------------------------------------------------
.break-div {
  // color: #bbbbbb;
  margin: 3px 0px;
  width: 75%;

  .paid-icon {
    padding-left: 3px;
  }

  .restricted-icon {
    padding-left: 3px;
  }

  button {
    position: relative;
    float: right;
  }
}

// Error Message in Modal
//---------------------------------------------------
.error-message {
  color: red;
  font-size: 16px;
  text-align: center;
}

//---------------------------------------------------
// MODAL PAGES
//---------------------------------------------------
.modal-wrapper {
  --border-radius: 4px;
}

.mini-modal .modal-wrapper {
  width: 80%;
}

.add-break-modal .modal-wrapper {
  height: 70%;
}

.detail-modal .modal-wrapper {
  height: 40%;
}

.clockout-modal .modal-wrapper {
  height: 40%;
}

.break-selector-modal .modal-wrapper {
  height: 50%;

  .apply-button {
    margin: 10px;
  }
}

.event-request-modal .modal-wrapper {
  height: 50%;
}

.recurrence-update-modal .modal-wrapper {
  height: 70%;
}

.orbital-dialog-modal .modal-wrapper {
  height: 30%;
  width: 70%;
}

.modal-shadow {
  opacity: 0.6 !important;
  background: #000 !important;
}

.label {
  font-size: 14px;
  font-weight: normal;
  margin: 14px 0px 10px;
  color: #222;
}

.checkbox {
  padding: 0;
  margin: 5px 0 5px 10px;
  transform: scale(1.5);
}

.select {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 8px;
  margin: 0;
}

.input {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 5px;
  margin: 0;
}

.textarea {
  border: 1px solid #ccc;
  border-radius: 4px;
}

.usage-message {
  margin: 20px 10px 10px 10px;
  font-size: 24px;
}

button:disabled > div > div.alert-radio-icon {
  visibility: hidden;
}

.clockout-button {
  font-weight: 1;
  --background: #931c21;
}

//---------------------------------------------------
// CLOCK PAGE
//---------------------------------------------------
.label-list-header {
  font-size: 16px;
  font-weight: normal;
  margin: 14px 0px 10px;
  color: #424242 !important;
}


.item {
  --background: #fff;
  --highlight-height: 0;  //Removes bottom highlight on daily note textarea in modal.
}

// .item:last-child {
//   --border-style: none;
// }

// Removes line on bottom of Item
//--------------------------------
.item-no-border {
  --border-style: none;
}

#clock-page {
  height: 100%;

  ion-list-header {
    font-weight: normal;
  }

  .scroll {
    background: #ededed;
  }

  .todaylist {
    margin-bottom: 110px;
    background: #fff;
  }

  .center {
    margin-left: auto;
    margin-right: auto;
    display: block;
  }
}

//---------------------------------------------------
// CLOCK: PAY PERIOD TAB
//---------------------------------------------------
.list-header {
  border-bottom: 1px solid #ddd;
}

.list-footer {
  border-top: 1px solid #ddd;
}

.payperiod-list-buttons {
  font-size: 14px;
  background-color: #fff;
  border: none;
  outline: none;
  padding: 0px;
}

//---------------------------------------------------
// CLOCK FOOTER PANEL
//---------------------------------------------------
.clock-controls {
  padding: 0px;

  .clockin-button {
    display: flex;
    margin: 0px auto;
    font-size: 14px;
    font-weight: 1;
    --background: green;
  }

  .clockout-button-container {
    display: flex;
    padding: 0px 3px;

    .clockout-button {
      font-size: 14px;
      font-weight: 1;
      --background: #931c21;
    }

    .break-button {
      font-size: 14px;
      font-weight: 1;
      --background: #daa520;
    }
  }
}

//---------------------------------------------------
// CLOCK FOOTER: Map
//---------------------------------------------------
.map-button {
  display: flex;
  margin: 0px auto;
}

#show-map-button {
  position: absolute;
  right: 0;
}

#map-container {
  height: 150px;
  border: 1px solid #ddd;
  margin-top: -3px;

  .map-canvas {
    height: 100%;
    background-color: orange;
    opacity: 0;
    transition: opacity 250ms ease-in;
  }

  .show-map {
    opacity: 1;
  }
}

//---------------------------------------------------
// MESSAGE: View Message Modal
//---------------------------------------------------
.request-button {
  margin: 20px 15px 0 10px;
  font-size: 20px;
  height: 50px;
  --background: var(--ion-color-secondary);
}

.outer-contentMessage {
  .messageIonRow-header {
    padding: 2px 10px;
    display: block;
    font-size: 14px;
    color: #999;

    .messageHeaderSubject {
      font-size: 18px;
      color: #424242;
      white-space: nowrap;
      display: inline-block;
      margin: 0px;
      padding: 4px 2px;
    }

    .messageHeaderDetails {
      display: inline-block;
      margin: 0px;
      padding: 2px;
    }

    .readCounter {
      text-align: right;
      float: right;
    }
  }

  .messageIonRow-content {
    line-height: 1.4;
    margin: 8px;
    display: block;

    .messageBlock {
      display: inline-block;
      padding-top: 14px;
      font-size: 15px;
      border-top: 1px solid #ebebeb;
    }
  }
}

//---------------------------------------------------
// Message Item
//---------------------------------------------------
.message {
  width: 100%;
  padding: 14px 18px;
  background-color: white;
  line-height: 1.4;
  text-align: left;

  .top-line {
    font-size: 15px;
    display: flex;
    justify-content: space-between;

    .subject {
      float: left;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    .time {
      float: right;
      padding: 0px 4px;
    }
  }

  .bottom-line {
    width: inherit;
    font-size: 14px;
    color: gray;
    text-align: left;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

//---------------------------------------------------
// Time Overview Display
//---------------------------------------------------
.segment-content {
  margin: 0 15px 0 15px;
}

//---------------------------------------------------
// No Messages or Events to Show
//---------------------------------------------------
.no-messagesOrEvents-text {
  margin: 20px;
  display: block;
  font-size: 16px;
  font-style: italic;
  color: #999;
}

//---------------------------------------------------
// Option Selector
//---------------------------------------------------
.option-selector {
  position: relative;
  float: right;
  .option {
    height: 40px;
    border: 1px solid darkgrey;
    border-radius: 5px;
    padding: 0 25px;
    margin-left: 5px;
  }

  .selected-option {
    background-color: darkgrey;
  }
}

//---------------------------------------------------
// PAGES
//---------------------------------------------------

// Main Tabs
//---------------------------------------------------
.hasMessages {
  font-size: 10px;
  color: white;
  background-color: red;
  position: fixed;
  margin-top: -10px;
  margin-left: 25px;
  border-radius: 100%;
  padding: 2px 6px;
  z-index: 100;
}

.noMessages {
  visibility: hidden;
  display: none;
}

// Login
//---------------------------------------------------
.login-content {
  --background: #cc5600;
  border: none;
  outline: none;

  .login-logo img {
    margin: 10% auto 4%;
    max-width: 86%;
    filter: brightness(0) invert(1);
    display: block;
  }

  .form-list {
    background-color: #cc5600;

    .item {
      background-color: #cc5600;

      .label {
        color: #eee;
        font-size: 16px;
        font-style: italic;
        margin: 17px 24px 0px;
      }

      .field-container {
        margin: 10px 18px;

        .input {
          --background: #eee;
          height: 50px;
          font-size: 24px;
          padding-left: 6px;
          width: 100%;
        }
      }
    }
  }

  .login-button {
    margin: 15px;
    font-size: 21px;
    min-height: 60px;
    // --background: #303c42;
    --background: #424242;
  }

  .button-disabled {
    cursor: default;
    opacity: 0.28;
    pointer-events: none;
  }
}

// Account Page
//---------------------------------------------------
#account-page {
  img {
    max-width: 140px;
    border-radius: 50%;
  }
}

// Menu Page
//---------------------------------------------------
.username-header {
  margin: 10px;
  font-size: 17px;
}

// Splash

#splash {
  --background: #eee;
  border: none;
  outline: none;

  .login-logo img {
    margin: 10% auto 4%;
    max-width: 86%;
    filter: brightness(0) invert(1);
    display: block;
  }

  footer {
    position: absolute;
    bottom: 10px;
    left: 10px;
    font-size: x-small;
  }
}
